/* #TESTING CLASSES */
.yellow {
  background-color: yellow;
}

.red {
  background-color: red;
}

.profile {
  width: 97%;
  max-width: 1000px;
  margin: 0.5rem auto;
  padding: 20px;
  border: 7px outset lightblue;
  border-radius: 10px;
  min-height: 92vh;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.brand__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.github__icon {
  margin-right: 0.3rem;
}

.profile__title {
  font-family: fantasy;
  color: lightblue;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  text-decoration: overline;
  text-align: center;
  font-size: medium;
  font-weight: bolder;
}

.search__container {
  display: flex;
  justify-content: space-around;
}

.search-input__container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border: 1px inset lightblue;
  border-radius: 5px;
  padding: 5px 12px;
  width: 270px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border: 1px solid lightblue;
  border-radius: 5px;
  padding: 5px;
  transition: border-color 0.2s ease-in-out;
}

.search-input__container:focus-within {
  border-color: rgb(23, 155, 199);
  outline: none;
}

.search__icon {
  margin-right: 5px;
  color: rgba(0, 0, 0, 0.8);
}

.profile__input {
  background: transparent;
  border: none;
  outline: none;
  background-color: transparent;
}

.profile__input:-webkit-autofill {
  -webkit-text-fill-color: #000;
  -webkit-box-shadow: 0 0 0 1000px white inset;
  /* THIS CODE REMOVES THE DEFAULT AUTOFILL BACKGROUND */
}

.profile__button {
  background-color: lightblue;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  padding: 5px 12px;
}

.profile__button:hover {
  outline: 1px solid rgb(23, 155, 199);
}

.profile__info {
  padding: 15px;
  margin-top: 0.5rem;
}

.profile__img {
  height: 170px;
  aspect-ratio: 1/1;
  border: 3px dotted rgb(23, 155, 199);
  border-radius: 50%;
}

.profile__figcaption {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  padding: 3px;
  margin-top: 0.8rem;
}

.profile__name {
  font-size: 1.5rem;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  text-align: center;
  display: inline-block;
  margin: 0 auto;
  font-weight: bolder;
  font-family: sans-serif;
  color: lightblue;
  border-radius: 5%;
}

.profile__bio {
  margin-top: 1rem;
  font-weight: bold;
  font-style: italic;
}

.profile__details {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  gap: 10px;
}

.loading,
.error {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 32px;
  color: rgb(23, 155, 199);
  height: 70vh;
}

.error__p {
  font-weight: bold;
  margin-bottom: 1.5rem;
}

.loading-indicator {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: lightblue;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% {
    opacity: 0.4;
    transform: scale(0.8);
  }
  50% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0.4;
    transform: scale(0.8);
  }
}